import { Component, OnInit, ViewChild, ChangeDetectorRef } from '@angular/core';
import { DATE_TIME_FORMAT } from 'src/app/messages';
import { FlatpickrOptions } from 'ng2-flatpickr';
import { PickListNewService } from 'src/app/service/pick-list-new.service';
import { DataService } from 'src/app/service/data.service';
import { ProductListPopupComponent } from 'src/app/common-module/product-list-popup/product-list-popup.component';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { CommonFunction } from 'src/app/common';
declare var $: any;
@Component({
  selector: 'app-add-picklist',
  templateUrl: './add-picklist.component.html',
})
export class AddPicklistComponent implements OnInit {

  exampleOptions: FlatpickrOptions = DATE_TIME_FORMAT;
  @ViewChild(ProductListPopupComponent, { static: false }) productAdd;
  orderNumberArray: any = []
  orderDetails: any;
  date_format: string = "M/d/yy";
  timezone: string
  time_format: string;
  currentDate = Date();
  loadingState = false;
  addForm: FormGroup;
  showLoader = false;
  formErrors = {
    apierror: null,
    order_id: null,
    product: null
  };
  isEditing = false;
  editId = null;
  productDetailList: any = []
  ProductItem: any = [];
  constructor(
    private pickListNewService: PickListNewService,
    private activatedRoute: ActivatedRoute,
    private cdr: ChangeDetectorRef,
    private dataService: DataService,
    private fb: FormBuilder,
    private router: Router,
  ) {
    if (this.activatedRoute.snapshot.paramMap.get('id')) {
      this.isEditing = true;
      this.editId = this.activatedRoute.snapshot.paramMap.get('id');
    }
    this.dataService.currentCompany.subscribe((response) => {
      if (response) {
        this.timezone = String(response.timezone.format);
        this.time_format = response.time_format.label;
        this.date_format = response.date_format.label;
      }
    });
    this.addForm = this.fb.group({
      sales_order_id: [null, Validators.compose([Validators.required])],
      receipt_date: [null, Validators.compose([Validators.required])],
      pick_list_date: [null],
      client_id: [null, Validators.compose([Validators.required])],
      product_detail_list: [],
      pick_list_no: [null, Validators.compose([Validators.required])],
    });
  }

  ngOnInit(): void {
    // if (this.isEditing) {
    //   this.getEditObject()
    // }
    this.getMasterData();
  }
  getMasterData() {
    this.pickListNewService.getMasterData().subscribe((responce) => {
      if (responce.success) {
        this.orderNumberArray = responce.data.sales_order ? responce.data.sales_order : [];
      }
    });
  }

  onOrderChange(data) {
    if (data && data.value) {
      this.getSalesOrderDetail(data.value);
      this.formErrors.order_id = null;
    }
  }
  getSalesOrderDetail(id) {
    this.pickListNewService.getSalesOrderDetail(id).subscribe((responce) => {
      if (responce.success) {
        this.orderDetails = responce.data;
        this.setdata();
      }
    });
  }
  setdata() {
    this.addForm.patchValue({
      client_id: this.orderDetails.client_id.value,
      pick_list_no: this.orderDetails.pick_list_no
    });
  }
  addProduct() {
    if (this.addForm.value.sales_order_id) {
      this.productAdd.addObject();
    } else {
      this.formErrors.order_id = "* Plese Select Order Number";
    }
  }
  onProductSubmit(data) {
    this.productDetailList = data;
  }
  submitForm(formData: any): void {
    console.log(formData.value);

    if (formData.valid) {
      if (this.ProductItem && this.ProductItem.length > 0) {
        this.formErrors.product = null;
        this.showLoader = true;
        const data = new FormData();
        data.append('sales_order_id', formData.value.sales_order_id);
        data.append('receipt_date', CommonFunction.changedateTimeFormate(formData.value.receipt_date[0]));
        data.append('pick_list_date', CommonFunction.currentTimeFormate());
        data.append('client_id', formData.value.client_id);
        data.append('pick_list_no', formData.value.pick_list_no);
        data.append('product_detail_list', JSON.stringify(this.ProductItem));

        if (this.isEditing) {
          this.pickListNewService.editPickListNew(this.editId, data).subscribe((response) => {
            this.showLoader = false;
            if (response.success) {
              this.router.navigateByUrl('/outbound/pick-list-new');
            } else {
              response.error.map(obj => {
                if (obj.hasOwnProperty('sales_order_id')) {
                  this.formErrors['sales_order_id'] = obj['sales_order_id'];
                } else {
                  this.formErrors['apierror'] = `* ${response.error}`;
                }
              });
            }
          }, (error) => {
            this.showLoader = false;
          });
        } else {
          this.pickListNewService.addPickListNew(data).subscribe((response) => {
            this.showLoader = false;
            if (response.success) {
              this.router.navigateByUrl('/outbound/pick-list-new');
            } else {
              response.error.map(obj => {
                if (obj.hasOwnProperty('sales_order_id')) {
                  this.formErrors['sales_order_id'] = obj['sales_order_id'];
                } else {
                  this.formErrors['apierror'] = `* ${response.error}`;
                }
              });
            }
          }, (error) => {
            this.showLoader = false;
          });
        }
      } else {
        this.formErrors.product = "* Please Add Product";
      }
    }
  }


  oneProduct(obj, checked) {
    if (checked) {
      let object = new Object();
      object['product_id'] = obj.product.value;
      object['price'] = obj.price;
      object['amount'] = obj.amount;
      object['product_qty'] = obj.product_qty;
      this.ProductItem.push(object);
      if (this.ProductItem.length === this.ProductItem.length) {
        $('#checkBoxproAll').prop('checked', true);
      }
    } else {
      $('#checkBoxproAll').prop('checked', false);
      this.ProductItem = this.ProductItem.filter((object) => {
        console.log(object);
        return String(object.product.value) !== String(obj.product.value);
      });
    }
  }
  markAllProduct(checked) {
    if (checked) {
      this.ProductItem = this.productDetailList.map((obj, index) => {
        let object = new Object();
        object['product_id'] = obj.product.value;
        object['price'] = obj.price;
        object['amount'] = obj.amount;
        object['product_qty'] = obj.product_qty;
        $('.pro-chk').prop('checked', true);
        return object;
      });
    } else {
      $('.pro-chk').prop('checked', false);
      this.ProductItem = [];
    }
  }


  ngAfterViewChecked() {
    this.cdr.detectChanges();
  }
}
